import React from 'react';
import { Helmet } from 'react-helmet';
import {Form, Grid, Transition, Segment, Button} from 'semantic-ui-react';
import axios from 'axios';

type Props = {

};

type State = {
    firstName: String,
    lastName: String,
    email: String,
    message: String
};

class Contact extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);

        this.onChangeFirstName = this.onChangeFirstName.bind(this);
    
        this.state = {
            firstName: "",
            lastName: "",
            email: "",
            message: ""
        };
    }

    onChangeFirstName(e : any) {
        this.setState({firstName: e.target.value});
    }
    onChangeLastName(e : any) {
        this.setState({lastName: e.target.value});
    }
    onChangeEmail(e : any) {
        this.setState({email: e.target.value});
    }
    onChangeMessage(e : any) {
        this.setState({message: e.target.value});
    }

    onSubmit(e:any) {
        e.preventDefault();

        const emailObject = {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            message: this.state.message,
            time: new Date().toISOString()
        }

        axios.post('', emailObject)
            .then((res) => {
                console.log(res.data);
            }).catch((error) => {
                console.log(error);
            })

        this.setState({
            firstName: "",
            lastName: "",
            email: "",
            message: ""
        })
    }

    render() {
        return (
        <>
            <Helmet>
                <title>Hafizuddin Jaafar | Contact Me</title>
            </Helmet>
            <div className="contact-me-layout">
                <div className='form-contact'>
                <Segment raised inverted>
                    <Grid stackable padded className='form-grid'>
                        <Grid.Row className='page-title'>
                            <h1>Contact Me</h1>
                        </Grid.Row>
                        <Transition.Group className='page-entry'>
                            <Grid.Row>
                                <Form inverted onSubmit={this.onSubmit} >
                                    <Form.Group widths='equal' >
                                        <Form.Input
                                            label="First Name"
                                            placeholder="First name..."
                                            name="firstName"
                                            value={this.state.firstName}
                                            onChange={this.onChangeFirstName}
                                            className="label" 
                                            />
                                            <Form.Input
                                            label="Last Name"
                                            placeholder="Last name..."
                                            name="lastName"
                                            value={this.state.lastName}
                                            onChange={this.onChangeLastName}
                                            className="label" 
                                            />
                                    </Form.Group>
                            
                                    <Form.Input
                                        label="Email"
                                        placeholder="Email Address..."
                                        name="emailAddress"
                                        value={this.state.email}
                                        onChange={this.onChangeEmail}
                                        className="label" 
                                        />
                                    <Form.TextArea
                                        label="Your questions, comments, or concerns"
                                        name="body"
                                        placeholder="Ask us anything!"
                                        value={this.state.message as string}
                                        onChange={this.onChangeMessage}
                                        className="label" 
                                        />
                                    <Button  floated='left' type="submit" color="teal" fluid>
                                        Submit
                                    </Button>
                                    
                                </Form>
                            </Grid.Row>
                        </Transition.Group>
                    </Grid>

                </Segment>
                </div>
            </div>
        </>
        )
    }
    
}

export default Contact;