import {useState} from 'react';
import {Menu, Header} from 'semantic-ui-react';
import { Link, useLocation } from 'react-router-dom';

import { FONT_SETTINGS } from '../util/data';

const Menubar = () => {
    let location = useLocation();
    const [activeItem, setActiveItem] = useState(location.pathname);
    return (
        <div className='menubar'>
            <Menu 
            inverted 
            pointing 
            secondary
            stackable
            size="massive" 
            color="black" 
            style={FONT_SETTINGS}
            >
                <Menu.Item
                    name="header"
                    children={
                        <>
                            <Header as="h1" inverted style={{margin:0,
                            fontFamily: 'Monument Extended, sans-serif'}}>
                                    HAFIZUDDIN JAAFAR
                                    <Header.Subheader as={Link}
                                    to="/about-me">
                                        Webdev | Data Science | Photography
                                    </Header.Subheader>
                            </Header>
                            
                        </>
                    }
                />
                <Menu.Menu position="right">
                    <Menu.Item
                        name='Gallery'
                        active={activeItem === 'gallery'}
                        onClick={() => setActiveItem('/gallery')}
                        as={Link}
                        to="/gallery"
                    />
                    <Menu.Item
                        name='Projects'
                        active={activeItem === 'projects'}
                        onClick={() => setActiveItem('/projects')}
                        as={Link}
                        to="/projects"
                    />
                    <Menu.Item
                        name='Animation'
                        active={activeItem === 'animation'}
                        onClick={() => setActiveItem('/animation')}
                        as={Link}
                        to="/animation"
                    />
                    <Menu.Item
                        name='Contact Me'
                        active={activeItem === 'contact-me'}
                        onClick={() => setActiveItem('/contact-me')}
                        as={Link}
                        to="/contact-me"
                    />
                </Menu.Menu>
                
            </Menu>
        </div>
    )
};

export default Menubar