
import axios from 'axios';

const MySejahtera = () => {
    let resultString;
    axios({
        url: 'https://raw.githubusercontent.com/MoH-Malaysia/covid19-public/main/mysejahtera/checkin_malaysia.csv',
        method: 'get',
        responseType: 'text'
    }).then((response) => {
        resultString = response;
    })
    return (
        <>
            {resultString}
        </>
    )
};
export default MySejahtera;