import {Image, Grid, Header} from 'semantic-ui-react';
import arrayGenerator from '../util/helper';
import { postNames, travelDescriptions } from '../util/data';

const Posts = () => {
    
    const RES_FILE_SOURCE:String = "res/img";
    const NUM_ELEM:number = 9;
    const filesArray = arrayGenerator(NUM_ELEM, RES_FILE_SOURCE);
    return (
        <div className="posts" >
            <Grid columns={3} stackable divided compact >
                {filesArray &&
                filesArray.map((elem,i)=> (
                <Grid.Column key={i} style={{ marginBottom: 20 }}>
                    {/* < PreviewImage src={elem} size="massive"/> */}
                    <div className='post-image'>
                        <Image src={elem} />
                        <div className='post-image-overlay'>
                            <span>
                                <Header as="h3" inverted
                                >
                                    {postNames[i]}
                                </Header>
                                <Header.Subheader as="h4">
                                    {travelDescriptions[i]}
                                </Header.Subheader>
                            </span>
                        </div>
                    </div>
                </Grid.Column>
                ))}
            </Grid>
        </div>
    )
};

export default Posts;