import { Grid, Image, Card} from 'semantic-ui-react';
import  Helmet  from 'react-helmet';
import { projectData } from '../util/data';



const Projects = () => {

    return (
        <>
            <Helmet>
                <title>Hafizuddin Jaafar | Projects</title>
            </Helmet>
            <div className='project-page'>
                <Card.Group centered doubling stackable>
                    {projectData &&
                    projectData.map((elem,i)=> (
                        <Card key={i}>
                            <Image src={elem.imageLink ? elem.imageLink : "https://react.semantic-ui.com/images/avatar/large/matthew.png"} wrapped ui={false} />
                            <Card.Content>
                            <Card.Header>{elem.name}</Card.Header>
                            {elem.date && 
                            <Card.Meta>
                                <span className='date'>{elem.date}</span>
                            </Card.Meta>}
                            <Card.Description>
                                {elem.description}
                            </Card.Description>
                            </Card.Content>
                        </Card>
                    ))}
                </Card.Group>
            </div>
        </>
    )
};

export default Projects;