import './App.css';

import { HashRouter as Router, useRoutes } from 'react-router-dom';
import HomepageLayout from './pages/HomepageLayout';
import Projects from './pages/Projects';
import AnimationPage from './pages/AnimationPage';
import Homepage from './pages/Homepage';
import AboutMe from './pages/AboutMe';
import Posts from './pages/Posts';
import Contact from './pages/Contact';

// Covid infographics page
import COVIDLayout from './pages/COVID/COVIDLayout';
import Epidemic from './pages/COVID/Epidemic';
import MySejahtera from './pages/COVID/MySejahtera';

const App = () => {
  let element = useRoutes([
    // A route object has the same properties as a <Route>
    // element. The `children` is just an array of child routes.
    {
      path: '/',
      element: <HomepageLayout />,
      children: [
        { path: '/', element: <Homepage /> },
        { path: '/projects', element: <Projects /> },
        { path: '/animation', element: <AnimationPage /> },
        { path: '/gallery', element: <Posts /> },
        { path: '/about-me', element: <AboutMe /> },
        { path: '/contact-me', element: <Contact />},
        { path: '/covid',
        element: <COVIDLayout />,
        children : [
          { path : 'epidemic', element: <Epidemic />},
          { path : 'mysejahtera', element: <MySejahtera />}
        ]
      }
      ]
    },
    
  ]);

  return element;
}

const AppWrapper = () => {
  return (
    <Router basename='/'>
        <App/>
    </Router>
  )
}
export default AppWrapper;
